import React, { useEffect, useRef } from "react";
import "../../App.css";

const ScrollLoop = ({ children }) => {
  // Создаем массив из копий элементов
  const clonedChildren = React.Children.map(children, (child, index) => {
    return React.cloneElement(child, { key: `cloned-${index}` });
  });

  return (
    <div className="scroll-loop-container">
      <div className="scroll-loop">
        {/* Помещаем копии элементов перед оригинальными элементами и после */}
        {clonedChildren}
        {children}
        {clonedChildren}
      </div>
    </div>
  );
};

export default ScrollLoop;
